.App {
  margin-top: 30px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.jumbotron h1 {
  font-size: 35px;
}

.leftNav {
  padding-bottom: 10px;
}

.leftNav .btn-primary,
.leftNav .btn-primary:hover {
  border-color: #999999 !important;
  background-color: #f47e30 !important;
}

.btn-primary.mLinks {
  padding: 2px 5px;
  font-size: 1.1em;
}

.formContent label,
.radiolist {
  min-width: 175px;
  font-weight: normal;
  clear: both;
  margin: 5px 0;
}

.formContent select,
.formContent input,
.formContent textarea,
.formContent .inputdiv {
  display: initial;
  min-width: 50%;
  width: initial;
  margin: 5px 0;
}

label.required:after {
  content: " *";
  color: #ff0000;
}

.embed .modal-header {
  background-color: #409ca9;
  color: white;
}

.embed .modal-header .close {
  color: white;
}

.embed .modal-body {
  max-height: calc(100vh - 200px);
  overflow-y: auto;
  padding: 25px;
}

.embed .modal-footer {
  background-color: lightgray;
}

.installation-modal .modal-header {
  background-color: #409ca9;
  color: white;
}

.installation-modal .modal-header .close {
  color: white;
}

.installation-modal .modal-body {
  max-height: calc(100vh - 100px);
  overflow-y: auto;
}

.installation-modal .modal-footer {
  background-color: lightgray;
}


