body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.vcenter {
  padding-top: 3px;
  margin-bottom: 0;
  text-align: left;
}

.loading-progress {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #fafafa;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  overflow: hidden;
}

.loading-progress-content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.loading-logo {
  width: 25%;
}

.loading-logo img {
  width: 100%;
}

.loading-progress-bar {
  width: 512px;
  margin-top: 112px;
}

#progressContainer {
  border-radius: 2px;
  height: 4px;
  background: #f5f5f5;
}

.primaryProgress {
  background: #f47e30;
  border-radius: 2px;
  height: 4px;
  width: 0%;
  max-width: 100%;
  -webkit-animation: progress 1s 1 forwards;
          animation: progress 1s 1 forwards;
}

@-webkit-keyframes progress {
  from {}
  to {
    width: 100%
  }
}

@keyframes progress {
  from {}
  to {
    width: 100%
  }
}

.loading-progress-message {
  font-family: initial;
  font-size: 16px;
  line-height: initial;
  color: #9e9e9e;
  margin-top: 56px;
}

.word-wrap {
  word-wrap: break-word;
  overflow-wrap: break-word;
}

/* Override SharePoint's button styling */

input[type=button],
input[type=reset],
input[type=submit],
button {
  min-width: initial;
}


